.shop-footer {
  width: 100%;
  height: 14vw;
  position: fixed;
  z-index: 2;
  left: 0;
  bottom: 0;
  background-color: #555555;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.shop-footer .cart {
  width: 14vw;
  height: 14vw;
  border: 1.5vw solid #444;
  position: relative;
  left: 2vw;
  bottom: 1vw;
  z-index: 2;
  background-color: #444;
  border-radius: 100%;
}
.shop-footer .cart .cart-img {
  width: 14vw;
  height: 14vw;
  background: url('../../../static/images/shop/cart.svg') no-repeat 30% 35%;
  border-radius: 100%;
  background-size: 50%;
}
.shop-footer .cart span {
  position: absolute;
  top: -2vw;
  right: -2vw;
  width: 5vw;
  height: 5vw;
  border-radius: 100%;
  background-color: red;
  color: #fff;
  font-size: 3vw;
  text-align: center;
  line-height: 5vw;
}
.shop-footer .have {
  background-color: #3190e8;
}
.shop-footer .footer-money {
  position: relative;
  width: 30vw;
  height: 100%;
  margin-left: - 18vw;
  padding: 1vw;
  color: #fff;
}
.shop-footer .footer-money span {
  font-size: 5vw;
}
.shop-footer .footer-money p {
  font-size: 3vw;
}
.shop-footer shortof-send {
  background-color: #535356;
}
.shop-footer .goto-pay {
  background-color: #4cd964;
}
.shop-footer span.pay {
  width: 30vw;
  height: 100%;
  line-height: 13vw;
  text-align: center;
  color: #fff;
  font-size: 4.5vw;
}



