.shop-comment {
  width: 100%;
}
.shop-comment .comment-info {
  width: 100%;
  height: 25vw;
  display: -ms-flexbox;
  display: flex;
  background-color: #fff;
  margin-bottom: 2vw;
}
.shop-comment .comment-info .info-left {
  width: 35vw;
}
.shop-comment .comment-info .info-left .left-number {
  width: 100%;
  height: 18vw;
  margin-top: 3vw;
  border-right: 1px solid #ccc;
}
.shop-comment .comment-info .info-left .left-number h2 {
  width: 100%;
  height: 7vw;
  line-height: 7vw;
  text-align: center;
  color: #f60;
}
.shop-comment .comment-info .info-left .left-number span {
  width: 100%;
  height: 4vw;
  line-height: 4vw;
  text-align: center;
  font-size: 3.5vw;
  margin-left: 10.5vw;
}
.shop-comment .comment-info .info-left .left-number p {
  width: 100%;
  height: 4vw;
  line-height: 4vw;
  text-align: center;
  font-size: 3vw;
}
.shop-comment .comment-info .info-right {
  width: 65vw;
  padding: 2vw 4vw;
}
.shop-comment .comment-info .info-right .right-star {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
}
.shop-comment .comment-info .info-right .right-star span {
  margin-right: 2vw;
}
.shop-comment .comment-info .info-right .right-time {
  width: 100%;
  font-size: 3vw;
}
.shop-comment .comment-info .info-right .right-time span:nth-child(2) {
  margin-left: 6.5vw;
}



