.shop_cart_warp {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}
.shop_cart_warp .shop_cart {
  width: 100vw;
  height: 90vw;
  position: absolute;
  opacity: 1;
  bottom: -80vw;
  left: 0;
  background-color: #fff;
}
.shop_cart_warp .shop_cart .cart_head {
  width: 100%;
  height: 11vw;
  background-color: #eceff1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.shop_cart_warp .shop_cart .cart_head .cart_head_span {
  display: inline-block;
  width: 16vw;
  height: 7vw;
  line-height: 7vw;
  text-align: center;
  font-weight: bold;
  color: #666;
}
.shop_cart_warp .shop_cart .cart_head .cart_head_left {
  border-left: 1vw solid #3190e8;
  margin-left: 3vw;
}
.shop_cart_warp .shop_cart .cart_head .cart_head_right {
  margin-left: 65%;
  font-size: 3vw;
}
.shop_cart_warp .up {
  -webkit-animation: 0.3s ease forwards up;
          animation: 0.3s ease forwards up;
}
.shop_cart_warp .down {
  -webkit-animation: 0.3s ease forwards down;
          animation: 0.3s ease forwards down;
}
@-webkit-keyframes up {
  from {
    bottom: -80vw;
  }
  to {
    bottom: 14vw;
  }
}
@keyframes up {
  from {
    bottom: -80vw;
  }
  to {
    bottom: 14vw;
  }
}
@-webkit-keyframes down {
  from {
    bottom: 14vw;
  }
  to {
    bottom: -70vw;
  }
}
@keyframes down {
  from {
    bottom: 14vw;
  }
  to {
    bottom: -70vw;
  }
}



